.profileHeader_wrapper {
    max-width: 90%;
    min-height: 300px;
    background: transparent;
    margin: 25px 15px 50px;
    border-radius: 10px;
    display: flex;
    justify-content: flex-start;
}

.profileHeader_info {
    max-width: 450px;
    margin-left: 50px;
}

h1,h5 {
    color: #fff;
}

.profileHeader_top_info,
.profileHeader_top_info h1 {
    margin-bottom: 25px;
}

.lfj_title {
    display: block;
    color: #fff;
    margin-bottom: 10px;
}

.lfj_title span {
    color: #73DAFB;
}

.lfj_desc {
    display: block;
    color: #fff;
}

.lfj_desc span {
    color: #73DAFB;
}

.lfj_status {
    display: block;
    color: #7C859D;
    margin-bottom: 10px;
}

.inputStatus {
    margin-bottom: 10px;
    height: 18px;
    background: #7C859D;
    color: white;
    border-radius: 2px;
    padding: 10px;
    width: 100%;
}

.about_info_wr h5 {
    color: #fff;
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 6px;
}
.contacts_wr {
    margin-top: 25px;
}

.about_info_wr p {
    color: #fff;
}

.img_wrapper {
    width: 300px;
    height: 300px;
}

.img_wrapper img {
    width: 100%;
    height: auto;
    object-fit: cover;
}

.social_icons {
    color: saddlebrown;
    margin-top: 15px;
    display: flex;
    justify-content: flex-start;
}

.social_icons li {
    margin-right: 15px;
}

.social_icons li a {
    color: #7C859D;
    display: inline-block;
    background: transparent;
    font-size: 24px;
    cursor: pointer;
    user-select: none;
}
.social_icons li:last-child {
    margin: 0;
}
.social_icons a:hover {
    color: #73DAFB;
}
.social_icons a:active {
    color: #73DAFB;
    transform: scale(0.90);
}

.preloader_wr {
    width: 75px;
    height: 75px;
    margin: 10vh auto;
}